.content {
  margin-top: 140px;
}
.videos {
  display: flex;
  flex-wrap: wrap;
}
.video__col {
  width: 30%;
  padding: 1rem;
}
.video__img {
  display: block;
  max-width: 100%;
  height: auto;
}
.video__content {
  padding: 1rem 0rem;
  display: flex;
  justify-content: start;
}
.video__user a img {
  width: 35px;
  height: 35px;
  border-radius: 50%;
}
.video__tittle {
  margin-left: 2rem;
  font-size: 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.user__name {
  margin-top: 0.3rem;
  font-size: 1rem;
  font-weight: 300;
  text-transform: none;
  color: #030303ab;
}
.video__date {
  font-size: 1rem;
  font-weight: 300;
  text-transform: none;
  color: #030303ab;
}
